.ConfirmAccountPage {
    --content-wrapper-width: 600px;

    &-Wrapper {
        @include before-desktop {
            padding-top: .6rem;
        }

        @include mobile {
            padding-top: .7rem;
        }
    }

    &-Button {
        margin-top: 1.2rem;

        @include mobile {
            margin-top: 1.4rem;
            width: 100%;
        }

        @include after-mobile {
            margin-left: 1.2rem;
        }
    }

    &-EmailInput {
        display: none;
    }

    &-Heading {
        font-size: 2.4rem;
        margin-bottom: 1.2rem;
        margin-top: 2.4rem;
        text-align: center;

        @include mobile {
            font-size: 2.8rem;
            margin-bottom: 1.4rem;
            margin-top: 2.8rem;
            display: none;
        }
    }

    &-Form {
        @include after-mobile {
            display: flex;
            align-items: flex-end;
            justify-content: center;
        }
    }

    input {
        @include mobile {
            width: 100%;
        }
    }
}
